.max .offers-container {
    height: 100%;
}
.max .offers-container .offers-card {
    width: 96%;
    margin: 0 auto;
    overflow: hidden;
    position: relative; 
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.max .offers-container .offers-card:nth-child(2),
.max .offers-container .offers-card:nth-child(4){
    height: 30%;
}
.max .offers-container .offers-card:nth-child(1),
.max .offers-container .offers-card:nth-child(3) {
    height: 18%;
}
.max .offers-container .offers-card .card1,
.max .offers-container .offers-card .card2,
.max .offers-container .offers-card .card3,
.max .offers-container .offers-card .card4  {
    width: 100%;
    height: 100%;
    shape-outside: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}
.max .offers-container .offers-card .card1 {
    background-image: url('./img/1.jpg');
}
.max .offers-container .offers-card .card4 {
    background-image: url('./img/3.jpg');
}
.max .offers-container .offers-card .card2 {
    background-image: url('./img/2.jpg');
}
.max .offers-container .offers-card .card3 {
    background-image: url('./img/4.jpg');
}
.max .offers-container .offers-card:nth-child(2) .back .clipPathForText,
.max .offers-container .offers-card:nth-child(4) .back .clipPathForText,
.max .offers-container .offers-card:nth-child(1) .back .clipPathForText,
.max .offers-container .offers-card:nth-child(3) .back .clipPathForText {
    display: none;
}
.max .offers-container .offers-card:nth-child(2) .back,
.max .offers-container .offers-card:nth-child(4) .back,
.max .offers-container .offers-card:nth-child(1) .back,
.max .offers-container .offers-card:nth-child(3) .back {
    width: 100%;
    height: 100%;
    padding: 0.5rem;
}
.max .offers-container .offers-card .back h2,
.max .offers-container .offers-card .back p {
    font-size: 2rem;
    color: #000;
    padding: 0.1rem;
    text-align: center;
}
@media(max-width: 500px) {
    .max .offers-container .offers-card:nth-child(1) .back p,
    .max .offers-container .offers-card:nth-child(3) .back p {
        font-size: 1.8rem;
    }
}
@media(max-width: 400px) {
    .max .offers-container .offers-card:nth-child(1) .back p,
    .max .offers-container .offers-card:nth-child(3) .back p {
        font-size: 1.6rem;
    }
}