.wrapper-happy-client {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}
/* ------------------------------
        ARROW
--------------------------------
*/
.arrow {
    width: 8%;
    height: 50%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: 0.2rem solid #fff;
    color: #fff;
    font-size: 2rem;
    font-weight: 700;
    writing-mode: vertical-lr;
    letter-spacing: 0.4rem;
    text-align: center;
    cursor: pointer;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
}
.prev-arrow {
    left: 0;
    background-color: #ba83c4;
    border-bottom-right-radius: 1rem;
    border-top-right-radius: 1rem;
}
.next-arrow {
    right: 0;
    background-color: #9198e5;
    border-bottom-left-radius: 1rem;
    border-top-left-radius: 1rem;
}
/* ------------------------------
        DOTS
--------------------------------
*/
.happy-client-dots-wrapper {
    position: absolute;
    bottom: 5%;
    width: 100%;
    z-index: 100;
}
.happy-client-dots-wrapper ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    width: 70%;
    height: 100%;
    margin: 0 auto;
}
.happy-client-dots-wrapper ul li {
    width: 10%;
    height: 0.5rem;
    background-color: rgba(255, 255, 255, 0.5);
    border: 0.15rem solid #fff;
    border-radius: 0.5rem;
    
}
.happy-client-dots-wrapper ul li.active {
    background-color: rgba(255, 255, 255, 1);
}
/* ------------------------------
       Card
--------------------------------
*/
.container-happy-client {
    margin: auto;
    padding: 0;
    box-sizing: border-box;
    width: 90%;
    height: 90%;
    display: flex;
    justify-content: space-around;
}
.card {
    position: relative;
    margin: auto;
    width: 80%;
    height: 100%;
    background-color: #fff;
    box-shadow: inset 30px 0 50px rgba(0, 0, 0, 0.5);
    display: none;
    flex-direction: column;
}
.show {
    display: flex;
}
.card-img-wrapper,
.card-text-wrapper {
    width: 100%;
    height: 50%;
    position: relative;
    background-color: #9198e5;
}
.card-img-back {
    width: 80%;
    height: 80%;
    position: relative;
    top: 2%;
    left: 2%; 
    border: 1rem double rgba(255, 255, 255, 1);
}
.imgBox {
    position: absolute;
    top: 10%;
    left: 10%;
    width: 100%;
    height: 100%;
    z-index: 50;
    overflow: hidden;
    box-sizing: border-box;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
    background-size: cover; 
}
.card-text-wrapper h2 {
    width: 60%;
    height: 3rem;
    position: absolute;
    top: -1.5rem;
    left: 5%;
    transform: skewX(-20deg);
    font-size: 1.5rem;
    background-color: #fff;
    text-align: center;
    padding-top: 0.3rem;
    border-bottom-left-radius: 1rem;
    border-top-right-radius: 1rem;
}
.details {
    width: 80%;
    height: 80%;
    background-color: rgb(238, 237, 237);
    border: 1rem double rgba(255, 255, 255, 1);
    position: absolute;
    bottom: 3%;
    right: 10%;
    padding: 1.2rem;
    box-sizing: border-box;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}

.details p {
    font-size: 1rem;
}
.img1 {
    background-image: url('./happyClient-img/1.jpg');
}
.img2 {
    background-image: url('./happyClient-img/2.jpg');
}
.img3 {
    background-image: url('./happyClient-img/3.jpg');
}
.img4 {
    background-image: url('./happyClient-img/4.jpg');
}
.img5 {
    background-image: url('./happyClient-img/5.jpg');
}

@media(max-width: 600px) {
    .card-img-back {
        width: 90%;
    }
    .details {
        width: 90%;
        left: 2%;
    }
}
@media(max-width: 420px) {
    .card-text-wrapper h2 {
        font-size: 1.2rem;
    }
    .details {
        height: 90%;
    }
}
@media(max-width: 370px) {
    .card-text-wrapper h2 {
        font-size: 1rem;
        padding-top: 0.5rem;
    }
    .details p {
        font-size: 0.85rem;
    }
}