.delivery__list-content{
    width: 100%;
    height: 90%;
    display: flex;
    flex-direction: column;
    list-style: none;
    overflow-y: scroll;
    padding: 0;
    margin: 0;
    box-sizing: border-box; 
}
.delivery__list-content li {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0.3rem 0;
    background-color: rgba(0, 0, 0, 0.5);
}
.del-list-remove {
    width: 2rem;
    height: 2rem;
    background-color: rgba(255, 255, 255, 0.5);
    border: 0.15rem solid #fff;
    border-radius: 0.3rem;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1rem;
}
.del-list-remove svg {
    width: 1.8rem;
    height: 1.8rem;
    fill: rgb(180, 28, 28);
}
.delivery__list-content li p {
    width: 50%;
    text-align: start;
}
.delivery__list-content li span {
    width: 20%;
}
.totalItem {
    width: 10%;
    padding-left: 0.1rem;
    background-color: rgba(255, 255, 255, 0.171);
    color: rgb(255, 255, 255);
    text-shadow: 0 0 0.2rem rgba(255, 5, 5, 1);
    margin-right: 1rem;
}
.delivery__list-content li p,
.delivery__list-content li span,
.totalItem {
    font-size: 2rem;
}
.delivery__list-content li span {
    text-decoration: underline;
}
@media(max-width: 620px) {
    .delivery__list-content li p {
        width: 45%;
    }
    .totalItem {
        width: 12%;
    }
}
@media(max-width: 520px) {
    .delivery__list-content li p,
    .delivery__list-content li span,
    .totalItem {
        font-size: 1.7rem;
    }
}
@media(max-width: 450px) {
    .totalItem {
        width: 15%;
    }
}