.home {
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    background-color:  #565a75;
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.cover {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}

.flex-center {
    display: flex;
    justify-content: center;
	align-items: center;
	
}
.left-box,
.right-box {
    width: 58%;
    height: 100%;
    transform: skewX(10deg);
    position: absolute;
    top: 0;
}
.left-box {
    background:linear-gradient(to top, #9198e5, #ba83c4 60%, #ba83c4 60%);
    left: -10%;
    border-bottom-right-radius: 5%;
    border-top-right-radius: 5%;
    margin-right: 2rem;
}
.right-box {
    background: transparent url(./home.jpg)  no-repeat;
    right: -10%;
    border-top-left-radius: 5%;
    border-bottom-left-radius: 5%;
    margin-left: 2rem;
}
.left-box h1 {
    font-size: 6rem;
    color: #fff;
    text-shadow: 0.25rem 0.25rem 0.1rem #000;
    mix-blend-mode: soft-light;
    /* -webkit-text-fill-color: transparent; */
}

.home-social {
    width: 5%;
    height: 60%;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    z-index: 3;
}
.home-social::before,
.home-social::after {
    content: '';
    width: 0.2rem;
    height: 15%;
    background-color: #fff;
}

.home-social svg {
    width: 1.5rem;
    height: 1.5rem;
    fill: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
    cursor: pointer;
    transition: all linear;
}

.home-social svg:hover {
    width: 1.8rem;
	height: 1.8rem;
    transform: translateX(-0.2rem) translateY(-0.2rem);
}
@media(max-width: 850px){
    .left-box {
        width: 65%;
    }
    .right-box {
        width: 50%;
    }
}
@media(max-width: 670px){
    .left-box {
        width: 80%;
        left: -15%;
    }
    .right-box {
        width: 40%;
        right: -10%;
    }
}
@media(max-width: 570px){
    .left-box {
        width: 100%;
        left: 0;
        margin-right: 0;
        transform: none;
        border-radius: 0;
    }
    .right-box {
        display: none;
    }
    .left-box h1 {
        transform: skewX(10deg);
        font-size: 8rem;
    }
    .home-social {
        width: 7%;
    }
}
