.center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
.text {
    font-size: 3rem;
    color: #fff;
    text-shadow: -0.1rem 0.1rem 0 #2a4158, 
				-0.2rem 0.2rem 0.5rem #71879a, 
				-0.25rem 0.25rem 1rem  #b1c5d4,
                -0.3rem 0.3rem 2rem #d6d7dc;
}

.offers-container {
    width: 95%;
    height: 95%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    border: 0.2rem solid #fff;
}
.offers-card {
    height: 49%;
    overflow: hidden;
    position: absolute;
}
.front {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
 	-moz-background-size: cover;
 	-o-background-size: cover;
    background-size: cover; 
    z-index: 2;
}
.card1 {
    background-image: url('./img/1.jpg');
    shape-outside: polygon(0 0, 100% 0, 90% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 90% 100%, 0 100%);
}
.card3 {
    background-image: url('./img/3.jpg');
    shape-outside: polygon(0 0, 100% 0, 80% 100%, 0 100%);
    clip-path: polygon(0 0, 100% 0, 80% 100%, 0 100%);
}
.card2 {
    background-image: url('./img/2.jpg');
    shape-outside: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(20% 0, 100% 0, 100% 100%, 0 100%);
}
.card4 {
    background-image: url('./img/4.jpg');
    shape-outside: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(10% 0, 100% 0, 100% 100%, 0 100%);
}
.sale-box {
    width: 10%;
    height: 100%;
    position: absolute;
    background-color: transparent;
    writing-mode: vertical-lr;
    text-align: center;
    text-transform: uppercase;
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.767);
    text-shadow: 0.15rem 0.15rem 0.2rem #000;
    letter-spacing: 0.3rem;
}
.offers-card:nth-child(2) .front .sale-box,
.offers-card:nth-child(3) .front .sale-box {
    width: 15%;
    padding: 0 0.3rem;
}
.offers-card:nth-child(1) .front .sale-box,
.offers-card:nth-child(4) .front .sale-box {
    font-size: 2.5rem;
    padding: 0 0.6rem;
}

.offers-card:nth-child(1), 
.offers-card:nth-child(2),
.offers-card:nth-child(1) .front .sale-box,
.offers-card:nth-child(2) .front .sale-box,
.offers-card:nth-child(1) .back, 
.offers-card:nth-child(2) .back {
    top: 0;
}
.offers-card:nth-child(3),
.offers-card:nth-child(4),
.offers-card:nth-child(3) .front .sale-box,
.offers-card:nth-child(4) .front .sale-box,
.offers-card:nth-child(3) .back,
.offers-card:nth-child(4) .back {
    bottom: 0;
}
.offers-card:nth-child(1),
.offers-card:nth-child(3),
.offers-card:nth-child(1) .front .sale-box,
.offers-card:nth-child(3) .front .sale-box,
.offers-card:nth-child(1) .back,
.offers-card:nth-child(3) .back {
    left: 0;
    
}
.offers-card:nth-child(2),
.offers-card:nth-child(4),
.offers-card:nth-child(2) .front .sale-box,
.offers-card:nth-child(4) .front .sale-box,
.offers-card:nth-child(2) .back,
.offers-card:nth-child(4) .back {
    right: 0;
    
}
.offers-card:nth-child(1) .back .clipPathForText,
.offers-card:nth-child(2) .back .clipPathForText,
.offers-card:nth-child(3) .back .clipPathForText,
.offers-card:nth-child(4) .back .clipPathForText {
    width: 15%;
    height: 100%; 
}

.offers-card:nth-child(2) .back .clipPathForText,
.offers-card:nth-child(4) .back .clipPathForText {
    float: left;
    shape-outside: polygon(0 0, 100% 0, 0 100%);
    clip-path: polygon(0 0, 100% 0, 0 100%);
}
.offers-card:nth-child(1) .back .clipPathForText,
.offers-card:nth-child(3) .back .clipPathForText {
    float: right;
    shape-outside: polygon( 100% 0, 100% 100%, 0 100%);
    clip-path: polygon(100% 0, 100% 100%, 0 100%);
}

.offers-card:nth-child(1) .back .clipPathForText,
.offers-card:nth-child(4) .back .clipPathForText {
    width: 10%;
}

.offers-card:nth-child(1),
.offers-card:nth-child(4){
    width: 70%;
}
.offers-card:nth-child(2),
.offers-card:nth-child(3) {
    width: 35%;
}
.offers-card:nth-child(2) .back,
.offers-card:nth-child(3) .back {
    width: 80%;
}
.offers-card:nth-child(1) .back,
.offers-card:nth-child(3) .back {
    padding-left: 1rem;
}
.offers-card:nth-child(2) .back h2,
.offers-card:nth-child(3) .back h2,
.offers-card:nth-child(2) .back p,
.offers-card:nth-child(3) .back p {
    font-size: 1.4rem;
}
.back {
    width: 90%;
    height: 100%;
    transition: 1s linear;
    background-color: #fff;
    position: absolute;
    display: none;
    z-index: 1;
}

.hidden {
    opacity: 0;
}
.showText {
    display: block;
}
.back h2,
.back p {
    font-size: 2rem;
    color: #000;
    padding: 0.1rem;
}


@media(max-width: 1250px) {
    .text {
        font-size: 2.5rem;
    }
}
@media(max-width: 1000px) {
    .offers-container {
        height: 90%;
        grid-template-rows: repeat(3, 30%);
        margin-top: 10%;
    }
}
@media(max-width: 750px) {
    .offers-container {
        height: 100%;
        margin-top: 0;
    }
}
@media(max-width: 650px) {
    .offers-card:nth-child(2) .back p,
    .offers-card:nth-child(3) .back p {
        font-size: 1.3rem;
    } 
    .offers-card:nth-child(1) .back p,
    .offers-card:nth-child(4) .back p {
        font-size: 1.8rem;
    } 
}
@media(max-width: 600px) {
    .offers-card:nth-child(1),
    .offers-card:nth-child(4){
        width: 60%;
    }
    .offers-card:nth-child(2),
    .offers-card:nth-child(3) {
        width: 45%;
    }
    .card3 {
        shape-outside: polygon(0 0, 100% 0, 86% 100%, 0 100%);
        clip-path: polygon(0 0, 100% 0, 86% 100%, 0 100%);
    }
    .card2 {
        shape-outside: polygon(14% 0, 100% 0, 100% 100%, 0 100%);
        clip-path: polygon(14% 0, 100% 0, 100% 100%, 0 100%);
    }
}
@media(max-width: 550px) {
    .offers-card:nth-child(1) .front .sale-box,
    .offers-card:nth-child(2) .front .sale-box,
    .offers-card:nth-child(3) .front .sale-box,
    .offers-card:nth-child(4) .front .sale-box {
        width: 20%;
        padding: 0 0.3rem;
        font-size: 2.5rem;
    }
}