.app {
  width: 100%;
  max-width: 1920px;
  min-width: 320px;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  background-color: #565a75;
  padding: 1rem auto;
  box-sizing: border-box;
  position: relative;
}

.container {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabletContainer,
.mobileContainer {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  margin-top: 10vh;
}

.wrapper{
  width: 100%;
  height: 700px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem auto;
}
.first {
  margin-top: 0;
}
.del {
  height: 1400px;
}

.tabletContainer .titel,
.mobileContainer .titel {
  width: 100%;
  height: 4rem;
  margin: 1rem 0;
  text-align: center;
  padding: auto 0;
  text-shadow: 0.15rem 0.15rem 2px #000, -0.1rem -0.1rem 10px rgba(0, 0, 0, 0.5);
  font-size: 3rem;
  color: #fff;
  mix-blend-mode: color-burn;
}

@media(max-width: 550px) {
  .max {
    height: 1400px;
  }
}
@media(max-width: 410px) {
  .titel:nth-of-type(5) {
    height: 8rem;
  }
}

