.team {
    width: 80%;
    margin: 1rem auto ;  
    position: relative; 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;  
}
.team-slider-wrapper{
    width: 500px;
    height: 90%;
    overflow: hidden; 
    
}
.team__slider-btn-prev,
.team__slider-btn-next {
    width: 10%;
    height: 10%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
}
.team__slider-btn-prev{
    left: 0;
    clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
}
.team__slider-btn-next{
    right: 0;
    clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
}
.carousel-indicator {
    position: absolute;
    left: 50%;
    bottom: -5%;
    transform: translateX(-50%);
    z-index: 15;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}
.carousel-indicator li {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 10px;
    height: 10px;
    margin: 0 0.2rem;
    background-color: #fff;
    border-radius: 50%;
    opacity: 0.5;
    transition: opacity 0.6s ease;
}
.carousel-indicator li:nth-child(1) {
    opacity: 1;
}
.team__card-container{
    width: 5500px;
    margin: 0;
    display: flex;
    align-items: center;
    transition: 0.5s all;
}
.team__card {
    width: 500px;
    height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    border: 0.4rem solid #fff;
	background-color: #fff;
    box-shadow: 0 0 2rem #aaa;
}

.team__card-img {
    width: 100%;
    height: 400px;
    position: relative;
    background-color: transparent;
    background-origin: content-box;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 2;
}

.team__social {
    width: 12rem;
    height: 10rem;
    position: absolute;
    bottom: -5rem;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: transparent;
    z-index: 3;
}


.team__social svg  {
    width: 5rem;
	height: 5rem;
	background-color: #9f62aa; 
    border: 0.5rem solid rgba(255, 255, 255, 1);
    fill: #fff;
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
    cursor: pointer;
    transition: all linear;
}

.team__card h2,
.team__card h3,
.team__card p {
    color: #000000;
    width: 90%;
    height: auto;
    text-align: start;
    margin: 0.3rem 0;
}

.team__card h2 {
    font-size: 3rem;
    padding-left: 3rem;
}

.team__card h3 {
    font-size: 2rem;
    padding-left: 2rem;
}
.team__card p {
    display: block;
    font-size: 1.3rem;
    font-style: italic;
    padding-left: 0.5rem;
}

.cover {
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
}

