@media(max-width: 800px) {
    .team {
        margin-bottom: 20%;
        margin-top: 5%;
    }
    .team__slider-btn-prev,
    .team__slider-btn-next {
        top: 101%;
        transform: none;
    }
    .team__slider-btn-prev{
        left: 12%;
    }
    .team__slider-btn-next{
        right: 12%;
    }
    .carousel-indicator {
        bottom: -7%;
    }
}

@media(max-width: 630px) {
    .team {
        width: 100%;
    }
}
@media(max-width: 500px) {
    .team-slider-wrapper{
        width: 400px;
    }
    .team__card {
        width: 400px;
        height: 500px;
    }

}