/*=================================================================== 
							SECTION "SOCIAL"
 =================================================================*/
.contactSocial {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.social {
	width: 100%;
	height: 90%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
}
.contact-wrapper {
	width: 90%;
	height: 20%;
	clip-path: polygon(90% 0, 100% 50%, 90% 100%, 0 100%, 12% 50%, 0 0);
	background-color: #fff;
}
.contact-clipPath-wrapper {
	width: 90%;
	height: 100%;
	margin-left: 2%;
	clip-path: polygon(90% 2%, 100% 50%, 90% 98%, 1% 98%, 14% 50%, 1% 2%);
	background-color: #ba83c4ec;
	position: relative;
	display: flex;
	align-items: center;
}
.contact-clipPath-wrapper p {
	width: 50%;
	font-size: 2.5rem;
	color: rgb(250, 243, 243);
	text-shadow: 0.1rem 0.1rem 0.3rem #000;
	text-align: center;
}
.contact__sociaIconlContainer {
	width: 18%;
	height: 80%;
	margin-left: 15%;
	border: 0.32rem solid #fff;
	border-radius: 2rem;	
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #be9ddf;
	box-shadow: 0.9rem 1rem 0.4rem rgba(0, 0, 0, 0.5);
}
.contact__sociaIconlContainer a {
	width: 5rem;
	height: 5rem;
	background-color: #565a75; 
	border: 0.13rem solid rgba(255, 255, 255, 0.5);
	border-radius: 50%;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
	cursor: pointer;
}

.contact__sociaIconlContainer a svg{
	display: block;
	width: 100%;
	height: 100%;
	fill: #fff;
}
@media(max-width: 650px){
	.contact-clipPath-wrapper p {
		font-size: 2.3rem;
	}
	.contact__sociaIconlContainer a {
		width: 4rem;
		height: 4rem;
	}
}
@media(max-width: 600px){
	.contact-clipPath-wrapper p {
		font-size: 2rem;
	}
}
@media(max-width: 550px){
	.contact-clipPath-wrapper p {
		font-size: 1.8rem;
		text-align: end;
	}
	.contact__sociaIconlContainer {
		height: 70%;
	}
	.contact__sociaIconlContainer a {
		width: 3.7rem;
		height: 3.7rem;
	}
}
@media(max-width: 450px){
	.contact-clipPath-wrapper p {
		font-size: 1.6rem;
	}
	.contact__sociaIconlContainer {
		width: 3.5rem;
		height: 3.5rem;
		border: none;
		background-color: transparent;
	}
	.contact__sociaIconlContainer a {
		width: 100%;
		height: 100%;
	}
}
@media(max-width: 400px){
	.contact-clipPath-wrapper p {
		font-size: 1.4rem;
	}
	.contact__sociaIconlContainer {
		width: 3.2rem;
		height: 3.2rem;
	}
}